"use client"

import React, { useState } from "react"

interface Recipe {
  id: string
  title: string
  description: string
  ingredients: string[]
  cookingTime?: string
  servings?: number
}

interface RecipeCardProps {
  recipe: Recipe
  onSave: (recipe: Recipe) => void
  isSaved?: boolean
}

const RecipeCard: React.FC<RecipeCardProps> = ({ recipe, onSave, isSaved = false }) => {
  const [rating, setRating] = useState<number>(0)
  const [isRating, setIsRating] = useState(false)
  const [showFullIngredients, setShowFullIngredients] = useState(false)

  const handleSave = async () => {
    try {
      onSave(recipe)
      
      // If user has rated, save the rating
      if (rating > 0) {
        const res = await fetch("/api/rating", {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({ recipeId: recipe.id, rating }),
        })
        
        if (!res.ok) {
          console.error("Failed to save rating")
        }
      }
    } catch (err) {
      console.error("Error saving recipe:", err)
    }
  }

  const handleRatingClick = (star: number) => {
    setRating(star)
    setIsRating(true)
    setTimeout(() => setIsRating(false), 300)
  }

  const displayIngredients = showFullIngredients 
    ? recipe.ingredients 
    : recipe.ingredients.slice(0, 5)

  return (
    <div className="group relative overflow-hidden rounded-lg border bg-card text-card-foreground shadow-sm transition-all hover:shadow-md">
      <div className="p-6">
        {/* Header */}
        <div className="flex items-start justify-between mb-3">
          <div className="flex-1">
            <h3 className="font-semibold text-lg leading-tight mb-1 group-hover:text-primary transition-colors">
              {recipe.title}
            </h3>
            <div className="flex items-center gap-3 text-xs text-muted-foreground">
              {recipe.cookingTime && (
                <span className="flex items-center gap-1">
                  ðŸ•’ {recipe.cookingTime}
                </span>
              )}
              {recipe.servings && (
                <span className="flex items-center gap-1">
                  ðŸ‘¥ {recipe.servings} servings
                </span>
              )}
            </div>
          </div>
          {isSaved && (
            <div className="flex items-center justify-center w-8 h-8 rounded-full bg-primary/10 text-primary">
              âœ“
            </div>
          )}
        </div>

        {/* Description */}
        <p className="text-sm text-muted-foreground mb-4 leading-relaxed">
          {recipe.description}
        </p>

        {/* Ingredients */}
        <div className="mb-4">
          <h4 className="text-sm font-medium mb-2">Ingredients:</h4>
          <div className="space-y-1">
            {displayIngredients.map((ingredient, index) => (
              <div key={index} className="flex items-center text-xs text-muted-foreground">
                <span className="w-1 h-1 bg-muted-foreground rounded-full mr-2 flex-shrink-0"></span>
                {ingredient}
              </div>
            ))}
            {recipe.ingredients.length > 5 && (
              <button
                onClick={() => setShowFullIngredients(!showFullIngredients)}
                className="text-xs text-primary hover:underline mt-1"
              >
                {showFullIngredients 
                  ? "Show less" 
                  : `+${recipe.ingredients.length - 5} more ingredients`
                }
              </button>
            )}
          </div>
        </div>

        {/* Rating */}
        <div className="flex items-center justify-between mb-4">
          <div className="flex items-center gap-1">
            <span className="text-xs text-muted-foreground mr-1">Rate:</span>
            {[1, 2, 3, 4, 5].map((star) => (
              <button
                key={star}
                onClick={() => handleRatingClick(star)}
                className={`text-lg transition-all hover:scale-110 ${
                  star <= rating 
                    ? "text-yellow-500" 
                    : "text-muted-foreground hover:text-yellow-400"
                } ${isRating ? "animate-pulse" : ""}`}
                disabled={isSaved}
              >
                {star <= rating ? "â˜…" : "â˜†"}
              </button>
            ))}
          </div>
          {rating > 0 && (
            <span className="text-xs text-muted-foreground">
              {rating}/5 stars
            </span>
          )}
        </div>

        {/* Action Button */}
        <button
          onClick={handleSave}
          disabled={isSaved}
          className={`w-full inline-flex items-center justify-center rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 h-9 px-3 ${
            isSaved
              ? "bg-muted text-muted-foreground cursor-not-allowed"
              : "bg-primary text-primary-foreground hover:bg-primary/90"
          }`}
        >
          {isSaved ? (
            <>
              <span className="mr-2">âœ“</span>
              Saved
            </>
          ) : (
            <>
              <span className="mr-2">ðŸ’¾</span>
              Save Recipe
            </>
          )}
        </button>
      </div>

      {/* Hover Effect */}
      <div className="absolute inset-0 bg-gradient-to-r from-primary/5 to-transparent opacity-0 group-hover:opacity-100 transition-opacity pointer-events-none" />
    </div>
  )
}

export default RecipeCard
