"use client"

import React, { useState } from "react"

interface IngredientInputProps {
  value: string
  onChange: (value: string) => void
  onSubmit: () => void
  loading?: boolean
}

const IngredientInput: React.FC<IngredientInputProps> = ({ 
  value, 
  onChange, 
  onSubmit, 
  loading = false 
}) => {
  const [focused, setFocused] = useState(false)

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onSubmit()
  }

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault()
      onSubmit()
    }
  }

  return (
    <div className="w-full">
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="space-y-2">
          <label 
            htmlFor="ingredients" 
            className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70"
          >
            Available Ingredients
          </label>
          <div className="relative">
            <textarea
              id="ingredients"
              className={`flex min-h-[80px] w-full rounded-md border border-input bg-background px-3 py-2 text-sm ring-offset-background placeholder:text-muted-foreground focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:cursor-not-allowed disabled:opacity-50 resize-none transition-all ${
                focused ? 'border-primary' : ''
              }`}
              placeholder="Enter your ingredients separated by commas (e.g., chicken, tomatoes, basil, garlic, onion)"
              value={value}
              onChange={(e) => onChange(e.target.value)}
              onKeyPress={handleKeyPress}
              onFocus={() => setFocused(true)}
              onBlur={() => setFocused(false)}
              disabled={loading}
              rows={3}
            />
          </div>
          <p className="text-xs text-muted-foreground">
            ðŸ’¡ Tip: The more ingredients you add, the better recipe suggestions you'll get!
          </p>
        </div>

        <button
          type="submit"
          disabled={loading || !value.trim()}
          className="w-full inline-flex items-center justify-center rounded-md text-sm font-medium ring-offset-background transition-colors focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2 disabled:pointer-events-none disabled:opacity-50 bg-primary text-primary-foreground hover:bg-primary/90 h-10 px-4 py-2"
        >
          {loading ? (
            <>
              <div className="mr-2 h-4 w-4 animate-spin rounded-full border-2 border-current border-t-transparent"></div>
              Generating Recipes...
            </>
          ) : (
            "Generate Recipes"
          )}
        </button>
      </form>

      {/* Ingredient Tags Preview */}
      {value.trim() && (
        <div className="mt-4 p-3 bg-muted rounded-md">
          <p className="text-xs font-medium text-muted-foreground mb-2">Ingredients to use:</p>
          <div className="flex flex-wrap gap-2">
            {value.split(',').map((ingredient, index) => {
              const trimmed = ingredient.trim()
              if (!trimmed) return null
              return (
                <span
                  key={index}
                  className="inline-flex items-center rounded-full bg-background px-2.5 py-0.5 text-xs font-medium text-foreground border"
                >
                  {trimmed}
                </span>
              )
            })}
          </div>
        </div>
      )}
    </div>
  )
}

export default IngredientInput
