"use client"

import React, { useState } from "react"

interface GroceryListProps {
  items: string[]
}

const GroceryList: React.FC<GroceryListProps> = ({ items }) => {
  const [checkedItems, setCheckedItems] = useState<Set<number>>(new Set())
  const [showChecked, setShowChecked] = useState(true)

  const handleItemCheck = (index: number) => {
    const newCheckedItems = new Set(checkedItems)
    if (newCheckedItems.has(index)) {
      newCheckedItems.delete(index)
    } else {
      newCheckedItems.add(index)
    }
    setCheckedItems(newCheckedItems)
  }

  const handleDownload = () => {
    const content = items.map((item, index) => 
      `${checkedItems.has(index) ? 'âœ“' : 'â˜'} ${item}`
    ).join('\n')
    
    const blob = new Blob([content], { type: 'text/plain' })
    const url = URL.createObjectURL(blob)
    const a = document.createElement('a')
    a.href = url
    a.download = 'grocery-list.txt'
    document.body.appendChild(a)
    a.click()
    document.body.removeChild(a)
    URL.revokeObjectURL(url)
  }

  const handleClearChecked = () => {
    setCheckedItems(new Set())
  }

  const checkedCount = checkedItems.size
  const totalCount = items.length

  if (!items || items.length === 0) {
    return (
      <div className="text-center py-8">
        <div className="text-4xl mb-4">ðŸ›’</div>
        <p className="text-muted-foreground">No items in your grocery list yet.</p>
        <p className="text-sm text-muted-foreground mt-2">
          Save some recipes to generate your grocery list!
        </p>
      </div>
    )
  }

  const displayItems = showChecked ? items : items.filter((_, index) => !checkedItems.has(index))

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h3 className="text-2xl font-semibold flex items-center gap-2">
            ðŸ›’ Grocery List
          </h3>
          <p className="text-sm text-muted-foreground mt-1">
            {checkedCount > 0 ? (
              <>
                {checkedCount} of {totalCount} items checked
                {checkedCount === totalCount && " - All done! ðŸŽ‰"}
              </>
            ) : (
              `${totalCount} items to shop for`
            )}
          </p>
        </div>
        
        <div className="flex items-center gap-2">
          <button
            onClick={() => setShowChecked(!showChecked)}
            className="text-xs px-3 py-1 rounded-full border border-input bg-background hover:bg-accent hover:text-accent-foreground transition-colors"
          >
            {showChecked ? "Hide Checked" : "Show All"}
          </button>
          <button
            onClick={handleDownload}
            className="text-xs px-3 py-1 rounded-full bg-primary text-primary-foreground hover:bg-primary/90 transition-colors"
          >
            ðŸ“¥ Download
          </button>
        </div>
      </div>

      {/* Progress Bar */}
      {totalCount > 0 && (
        <div className="w-full bg-muted rounded-full h-2">
          <div
            className="bg-primary h-2 rounded-full transition-all duration-300 ease-out"
            style={{ width: `${(checkedCount / totalCount) * 100}%` }}
          />
        </div>
      )}

      {/* Action Buttons */}
      {checkedCount > 0 && (
        <div className="flex justify-end">
          <button
            onClick={handleClearChecked}
            className="text-xs text-muted-foreground hover:text-foreground transition-colors"
          >
            Clear all checked items
          </button>
        </div>
      )}

      {/* Items List */}
      <div className="space-y-2">
        {displayItems.length === 0 ? (
          <div className="text-center py-8 text-muted-foreground">
            <p>All items are checked! ðŸŽ‰</p>
            <button
              onClick={() => setShowChecked(true)}
              className="text-primary hover:underline text-sm mt-2"
            >
              Show checked items
            </button>
          </div>
        ) : (
          <div className="grid gap-2">
            {displayItems.map((item, displayIndex) => {
              const originalIndex = showChecked 
                ? displayIndex 
                : items.findIndex((originalItem, idx) => 
                    originalItem === item && !checkedItems.has(idx)
                  )
              const actualIndex = showChecked ? displayIndex : items.indexOf(item)
              const isChecked = checkedItems.has(actualIndex)
              
              return (
                <div
                  key={`${item}-${actualIndex}`}
                  className={`flex items-center space-x-3 p-3 rounded-lg border transition-all hover:bg-accent/50 ${
                    isChecked 
                      ? "bg-muted/50 text-muted-foreground" 
                      : "bg-background"
                  }`}
                >
                  <button
                    onClick={() => handleItemCheck(actualIndex)}
                    className={`flex items-center justify-center w-5 h-5 rounded border-2 transition-all ${
                      isChecked
                        ? "bg-primary border-primary text-primary-foreground"
                        : "border-input hover:border-primary"
                    }`}
                  >
                    {isChecked && <span className="text-xs">âœ“</span>}
                  </button>
                  
                  <span 
                    className={`flex-1 transition-all ${
                      isChecked ? "line-through" : ""
                    }`}
                  >
                    {item}
                  </span>
                  
                  {isChecked && (
                    <span className="text-xs text-muted-foreground">âœ“</span>
                  )}
                </div>
              )
            })}
          </div>
        )}
      </div>

      {/* Summary */}
      <div className="mt-6 p-4 bg-muted/50 rounded-lg">
        <div className="flex items-center justify-between text-sm">
          <span className="text-muted-foreground">Shopping Progress</span>
          <span className="font-medium">
            {checkedCount}/{totalCount} items
          </span>
        </div>
        {checkedCount === totalCount && totalCount > 0 && (
          <p className="text-xs text-primary mt-2">
            ðŸŽ‰ Congratulations! You've got everything on your list!
          </p>
        )}
      </div>
    </div>
  )
}

export default GroceryList
